<template>
  <el-dialog
      :title="$t('user.bindGoogleVerify.bindTitle')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="588px" class="google-dialog">
      <div>
        <p>{{ $t('user.bindGoogleVerify.des.part1') }}</p>
        <div class="title"><span>1</span>{{ $t('user.bindGoogleVerify.des.part2') }}</div>
        <p>{{ $t('user.bindGoogleVerify.des.part3') }}</p>
        <div class="clearfix download" >
          <el-button type="primary" class="pull-left" @click="handleAppLink"> <img src="/static/images/apple.png" alt=""> APP Stove</el-button>
          <el-button type="primary" class="pull-left" @click="handleGoogleLink"> <img src="/static/images/google.png" alt=""> Google Play</el-button>
        </div>
        <div class="title"><span>2</span>{{ $t('user.bindGoogleVerify.des.part4') }}</div>
        <div>{{ $t('user.bindGoogleVerify.des.part5') }}</div>
        <p class="c-red">{{ $t('user.bindGoogleVerify.des.part6') }}</p>
        <div class="clearfix google-key">
          <div class="pull-left"><img class="google-code-a" :src="googleInfo.qr_code_url"></div>
          <div class="pull-left">
            <p>{{ $t('user.bindGoogleVerify.des.part7') }}</p>
            <div class="clearfix"><div class="key google-key-a pull-left " >{{ googleInfo.secret_key }}</div><el-button class="pull-left violet-border-btn" v-clipboard:copy="googleInfo.secret_key" v-clipboard:success="handleCopySuccess" v-clipboard:error="handleCopyError">{{ $t('copy') }}</el-button></div>
          </div>
        </div>
        <div class="title"><span>3</span>{{ $t('user.bindGoogleVerify.des.part8') }}</div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="form">
          <el-form-item label="" prop="pass_double">
            <el-input v-model.trim="ruleForm.pass_double"></el-input>
          </el-form-item>
          <el-button type="primary"  @click="phoneSubmit('ruleForm')">{{ $t('dialog.sure') }}</el-button>
        </el-form>
      </div>
    </el-dialog>
</template>

<script>
  import { getSecretAndCode, openGoogleAuth } from '@/api/user'

  export default {
    props: ['dialogVisible'],
    data() {
      return {
        googleInfo: {},
        ruleForm: {
          pass_double: null
        },
        rules: {
          pass_double: [
            { required: true, message: this.$t('errorTips.noGoogleCode'), trigger: 'blur' }
          ]
        },
        validCode: true,
        validTip: this.$t('verifyCode.title'),
        questions: {}
      }
    },
    computed: {
    },
    created() {
      this.initGoogleInfo()
    },
    methods: {
      initGoogleInfo() {
        getSecretAndCode().then(res => {
          console.log('谷歌信息', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.googleInfo = res.data
        }).catch(() => {
        })
      },
      handleCopySuccess() {
        this.$message.success(this.$t('errorTips.copySuccess'))
      },
      handleCopyError() {
        this.$message.error(this.$t('errorTips.copyError'))
      },
      handleCloseDialog() {
        this.$emit('closeDialog', 'googleCentifyDialogVisible')
      },
      handleAppLink() {
        window.location.href = 'https://itunes.apple.com/us/app/google-authenticator/id388497605?mt=8'
      },
      handleGoogleLink() {
        window.location.href = 'https://play.google.com/store/apps/details?id=com.google.android.apps.authenticator2'
      },
      phoneSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.ruleForm }
            openGoogleAuth(data).then(res => {
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('errorTips.googleCodeSuccess'))
              this.handleCloseDialog()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
  
    }
  }
</script>

<style lang="less" scoped>
  .google-dialog{
    line-height: 25px;
    color: #7f838d;
    p{
      padding-bottom: 18px;
    }
    .download{
      button{
        width: 140px;
        padding: 8px 0;
        margin: 0;
        margin-bottom: 25px;
        img{
          vertical-align: middle;
          margin-right: 9px;
        }
        &:first-child{
          margin-right: 20px;
        }
      }
      
    }
    .title{
      margin-bottom: 10px;
      span{
        width: 23px;
        height: 23px;
        display: inline-block;
        margin-right: 10px;
        border-radius: 50%;
        border: #c0bfc1 1px solid;
        background: #f1eff4;
        line-height: 23px;
        text-align: center;
      }
    }
    .payway{
      margin-bottom: 15px;
      label{
        width: 86px;
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 49px;
      color: #b4b6be;
      font-size: 12px;
    }
    .google-key{
      padding-bottom: 40px;
    }
    .google-key .pull-left img {
      height: 80px;
      width: 80px;
      margin-right: 6px;
    }
    .google-key .pull-left{
      p{
        padding-bottom: 13px;
      }
    }
    .google-key .pull-left .key {
      width: 210px;
      height: 40px;
      line-height: 40px;
      border-radius: 5px;
      border: 1px solid #e5e2e9;
      background: #f1eff4;
      text-align: center;
      margin-right: 8px;
  }
  .form{
    /deep/ input{
      margin-top: 10px;
    }
    button{
      margin: 0;
    }
  }
  }
</style>



